width: 1.375rem;\n    height: 1.375rem;\n  }\n}\n\n.pgv-step-detail-header__actions {\n  display: flex;\n  align-items: center;\n  gap: 0.65rem;\n\n  & > button {\n    min-width: 38px;\n    padding-inline: 0 !important;\n  }\n}\n\n.pgv-show-more-logs {\n  position: sticky;\n  top: calc(var(--header-height) + 0.375rem + 0.375rem + 2.375rem);\n  background: var(--card-background);\n  box-shadow: 0 0 0.375rem var(--card-background);\n  z-index: 10;\n  border-radius: 0.375rem;\n  width: 100%;\n}\n\n.console-text div {\n  text-indent: 0;\n  display: inline-block;\n}\n\npre.console-output-line {\n  text-align: left;\n  padding: 0;\n  margin: 0;\n  border-radius: 0;\n}\n\na.console-line-number {\n  text-align: right;\n  margin-right: 14px;\n  color: var(--text-color-secondary);\n  white-space: nowrap;\n  display: inline-block;\n  flex-shrink: 0;\n  user-select: none;\n}\n\ndiv.console-output-line-anchor {\n  position: relative;\n  // This is the width of the '.jenkins-breadcrumbs' div + a little extra.\n  top: -50px;\n  visibility: hidden;\n}\n\ndiv.console-output-line {\n  position: relative;\n  display: flex;\n  padding-block: 2px;\n\n  &::before {\n    content: \"\";\n    position: absolute;\n    inset: 0;\n    background-color: color-mix(\n      in srgb,\n      var(--text-color-secondary) 5%,\n      transparent\n    );\n    // TODO - var fallback can removed after baseline is moved >= 2.496\n    border: var(--jenkins-border-width, 2px) solid\n      color-mix(in srgb, var(--text-color-secondary) 2.5%, transparent);\n    border-radius: 0.375rem;\n    opacity: 0;\n    transition: opacity var(--standard-transition);\n    pointer-events: none;\n  }\n\n  &:hover {\n    &::before {\n      opacity: 1;\n    }\n  }\n}\n"],"sourceRoot":""}]);
